export default function AllLettersPage() {
  return (
    <div className="all-letters-page">
      <div className={"letters-container coll"}>
        {letters.map((letter, key) => {
          let audio = new Audio(`./sounds-albayan-2/letter-${key + 1}.mp3`);
          return (
            <div className="letter" key={key}>
              <div
                className="box"
                onClick={() => {
                  audio.play();
                }}
              >
                <span className={key < 6 ? "red" : key < 29 ? "green" : "blue"}>
                  {letter}
                </span>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
